html {
  box-sizing: border-box;
  font-size: 100%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  background-color: #F5F5F5;
}

a {
  text-decoration: none;
  color: #b3d82b;
}

p {
  font-family: "Open Sans", sans-serif;
}

.section-title {
  color: #5B2A84;
  font-weight: bold;
}

.section-link {
  font-size: 0.8rem;
  color: #231F20;
}
.section-link:hover {
  color: #b3d82b;
}

.news-tags {
  color: #411763;
}

.news-title {
  color: #231F20;
}

.news-date {
  color: #424242;
}

.calendar-date {
  color: #411763;
}

.calendar-month {
  color: #231F20;
}

.calendar-event {
  color: #411763;
}

.calendar-time {
  color: #424242;
}

.content-column h1, .content-column h2, .content-column h3, .content-column h4, .content-column h5, .content-column h6 {
  color: #5B2A84;
}

.is-style-kings-file-block,
.editor-styles-wrapper .is-kings-file-block {
  background-color: #e5ebc3;
  padding: 1rem;
  margin-bottom: 3px;
}
.is-style-kings-file-block a,
.editor-styles-wrapper .is-kings-file-block a {
  color: #231F20;
  font-weight: 500;
}
.is-style-kings-file-block a:hover,
.editor-styles-wrapper .is-kings-file-block a:hover {
  color: #5B2A84;
}
.is-style-kings-file-block a::before,
.editor-styles-wrapper .is-kings-file-block a::before {
  font: var(--fa-font-solid);
  content: "\f15b";
  font-size: 1rem;
  color: #424242;
  padding-right: 1rem;
}
.is-style-kings-file-block a::before:hover,
.editor-styles-wrapper .is-kings-file-block a::before:hover {
  color: #5B2A84;
}

.is-style-kings-quote-block p,
.editor-styles-wrapper .is-kings-quote-block p {
  font-family: "Cormorant", serif;
  font-weight: 300 !important;
}

.custom #my-search {
  float: right;
  width: 306px;
}

.custom form.search_form_visible {
  width: 306px;
}

.custom form.search_form_visible #s {
  background: #fff;
  border: 1px solid #000;
  font-size: 12px;
  margin: 0 6px 0 0;
  padding: 4px;
  width: 215px;
  float: left;
}

.custom #my-search input#searchsubmit.my-search {
  background: #000;
  color: #fff;
  cursor: pointer;
  float: right;
  padding: 4px;
}